// Angular
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { NgbDate, NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { DatePipe } from '@angular/common'
import { PartialsModule } from '../../partials/partials.module';
import { MaterialPreviewModule } from '../../partials/content/general/material-preview/material-preview.module';
import { SettingsService, PatientsService, DoctorsService, DataSharingService } from '../../../core/application';


// Components

// Import AG Grid
import { AgGridModule } from 'ag-grid-angular';

// addons
import { MatInputModule } from '@angular/material';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'

import { AngularSlickgridModule } from 'angular-slickgrid'
import { SettingsComponent } from './settings.component';
import { SimpleSettingComponent } from './simple-setting/simple-setting.component';

const routes: Routes = [
    {
        path: '',
        component: SettingsComponent,
        children: [
            {
                path: 'simple-setting/:setting',
                component: SimpleSettingComponent
            }
        ]
    }
];

@NgModule({
    imports: [
        CommonModule,
        HttpClientModule,
        RouterModule.forChild(routes),
        FormsModule,
        ReactiveFormsModule,
        PartialsModule,
        MaterialPreviewModule,
        NgbModule,
        MatInputModule,
        AgGridModule.withComponents([]),
        AngularSlickgridModule.forRoot()
    ],
    exports: [],
    providers: [SettingsService, NgbActiveModal, DataSharingService, DatePipe],
    entryComponents: [],
    declarations: [
        SettingsComponent,
        SimpleSettingComponent
    ]
})
export class SettingsModule { }
