import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Column, GridOption, OnEventArgs, AngularGridInstance, GridService, Formatters } from 'angular-slickgrid';
import { SettingsService } from '../../../../core/application';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { LayoutUtilsService, MessageType } from '../../../../core/_base/crud';


@Component({
  selector: 'kt-simple-setting',
  templateUrl: './simple-setting.component.html',
  styleUrls: ['./simple-setting.component.scss']
})
export class SimpleSettingComponent implements OnInit {

  setting = '';

  settingGrid: AngularGridInstance;
  grid: any;
  gridService: GridService;
  dataView: any;

  columnDefinitions: Column[] = [];
  gridOptions: GridOption = {};
  dataset: any = [];

  description = '';

  constructor(private route: ActivatedRoute,
              private layoutUtilsService: LayoutUtilsService,
              private settingService: SettingsService,
              private modalService: NgbModal) { }

  settingGridReady(angularGrid: AngularGridInstance) {
    this.settingGrid = angularGrid;
    this.dataView = angularGrid.dataView;
    this.grid = angularGrid.slickGrid;
    this.gridService = angularGrid.gridService;
  }
  ngOnInit() {
    this.columnDefinitions = [
      {
        id: 'id', name: 'Id', field: 'id', sortable: true, width: 30,
        formatter: Formatters.editIcon,
        onCellClick: (e: Event, args: OnEventArgs) => {
          const id = args.dataContext.id;

        }
      },
      { id: 'description', name: 'Description', field: 'description', sortable: true, filterable: true }
    ];
    this.gridOptions = {
      enableAutoResize: true,       // true by default
      enableCellNavigation: true,
      enableFiltering: true,
      enablePagination: true,
      pagination: {
        pageSizes: [10, 15, 20, 25, 30, 40, 50, 75, 100],
        pageSize: 10,
        totalItems: 0
      },
    };


    this.route.paramMap.subscribe(params => {
      console.log(params);
      this.setting = params['params']['setting'];
      this.settingService.getAll(this.setting).subscribe(
        res => this.dataset = res,
        err => console.log('errro'),
        () => console.log('finish')
      );
    });
  }

  open(content) {
    this.modalService.open(content);
  }

  addNew() {
    const message = `ajouté avec succés.`;
    this.settingService.addNew(this.setting,this.description).subscribe(
      res => console.log(res),
      err => {
        this.layoutUtilsService.showActionNotification('erreur', MessageType.Delete, 5000, true, true);
      },
      () => {
        this.description = ''
        this.layoutUtilsService.showActionNotification(message, MessageType.Create, 5000, true, true);
        this.settingService.getAll(this.setting).subscribe(
          res => this.dataset = res,
          err => console.log('errro'),
          () => console.log('finish')
        );
      }
    )
  }
}
